<template>
	<div class="ssglsc">
		<input type="text" placeholder="请搜索诗词" v-model="filtertext">
		<ul class="suggestions" >
			<li v-for="item in filter">{{item}}</li>
		</ul>
	</div>
</template>

<script>

	import axios from 'axios'
	export default{
		data(){
			return{
				shici:[],
				filtertext:'',
				a:['a','b','c','d']
			}
		},
		mounted(){
			axios
			.get('https://gist.githubusercontent.com/soyaine/81399bb2b24ca1bb5313e1985533c640/raw/bdf7df2cbcf70706c4a5e51a7dfb8c933ed78878/TangPoetry.json')
	        .then (res => {
	        	this.shici = res.data
	        })
		},
		computed:{
			filter(){
				let result = [];

				this.shici.forEach(item =>{
					result.push(item.detail_text) 
				})

				return result.filter((element) =>{
					return element.match(this.filtertext)
				})
			}	
		}
	}
</script>

<style lang="scss" scoped>
	.ssglsc{
		width: 100%;
		text-align: center;
		input{
			width: 90%;
			height: 1rem;
			padding: .2rem;
			font-family:'Kaiti', 'helvetica neue';
			border:.1rem solid #f7f7f7;
			box-sizing: border-box;
			border-radius:.1rem;
			text-align: center;
		}
		.suggestions{
			position: relative;
			li{
				width: 70%;
				background:white;
			    border-bottom: .02rem solid #D8D8D8;
			    box-shadow: 0 0 .2rem rgba(0, 0, 0, 0.14);
			    padding:.4rem;
			    margin: .02rem auto;
			    display:flex;
			    justify-content:center;
			    font-size: .28rem;
			    &:nth-child(even){
			    	transform: perspective(1rem) rotateX(3deg) translateY(2px) scale(1.002);
			    	background: linear-gradient(to bottom,  #ffffff 0%,#EFEFEF 100%);
			    }
			     &:nth-child(odd){
			    	transform: perspective(1rem) rotateX(-3deg) translateY(3px);
			    	background: linear-gradient(to bottom,  #ffffff 0%,#EFEFEF 100%);
			    }
			}
		}

	}
</style>